*{
  box-sizing: border-box;
  /* outline: 3px solid rgba(162, 243, 162, 0.4); */
}
:root {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 62.5%;
  line-height: 24px;
  font-weight: 400;
  
}

html, body,h1,h2,h3,p{
  margin: 0;
  padding: 0;
}

body{
  font-size: 1.6rem;
  width: 100%;
  color: #3d3d3d;
}

.header{
  height: 90vh;
  background-color: #3d3d3d;
  background-image: url(./road-into-city.jpg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: overlay;
  padding: 2em 1.2em 0;
  color:aliceblue;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.header__logo{
  width: 5rem;
  margin-bottom: 1em;
}

.header__title{
  color:#33f146;
  letter-spacing: .2rem; 
  text-align: center;
  margin: 1em;
  display: inline-block;
  /* max-width: 50%; */
}

.header__title--primary{
  font-family: 'Courgette', "cursive";
  font-size: 6rem;
}

.header__title--secondary {
  color:aliceblue;
  text-transform: uppercase;
  font-size: 2rem;
  display: block;
  font-size: 1.5rem;
  letter-spacing: 0.01rem;
  /* background-color: #B71E1E; */

}

.header__phrase{
  font-family: "Inter";
  font-weight: 700;
  font-size: 2em;
  text-transform: capitalize;
  letter-spacing: 1px;
  line-height: 1.2;
  text-align: center;
  padding: 3em 0 0.2em;

}

.header__tagline{
  font-family: "Inter";
  font-weight: 700;
  font-size: 1.1em;
  text-transform: capitalize;
  letter-spacing: 1px;
  line-height: 1.5;
  text-align: center;
  color: #c5c5c5;
  margin-bottom: 2.5em;

}

.header__cta{
  background-color: #8f1818;
  color: #FFFFFF;
  border: none;
  border-radius: 0.3em;
  text-decoration: none;
  font-size: 1.5em;
  padding: 0.75em 1.5em;
}

.header__cta:focus,
.header__cta:hover{
  background-color: #d52e2e;
  box-shadow: 6px 19px 21px -18px rgba(38, 38, 38, 0.67);
  -webkit-box-shadow: 6px 19px 21px -18px rgba(38, 38, 38, 0.67);
  -moz-box-shadow: 6px 19px 21px -18px rgba(38, 38, 38, 0.67);

}

.section{
  padding: 3.5em 0 4.5em;
}

/* =============== BENEFITS =============== */

.benefit{
  width: 100%;
  /* max-width: 300px; */
  /* margin: 1.5em; */
  
}

.benefit__image{
  --width: 100%;
  width: var(--width);
  margin-bottom: 1.5em;
}

.benefit__heading{
  color: #606060;
  font-size: 1.5em;
  padding: 1em 0 0;
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 1em;
}

.benefit__body{
  color: #606060;
  padding: 0 0.5em 0.5em;
  font-size: 1.1em;
  line-height: 1.6;
  text-align: center;
  
}

/* =============== FEATURES =============== */

.section__features{
  background-color: #BBFFCA;
  color:rgba(61, 61, 61, 0.8);
  text-align: center;
}

.section__features--title{
  font-size: 2.25em;
  line-height: 1.3;
  letter-spacing: 0.1rem;
  padding: 0 1em;
  margin: 0.5em 0 1em;
}

.feature__image{
  width: 60px;
  margin: 1em 0 0.5em;
}

.feature__heading{
  text-transform: capitalize;
  text-align: center;
  font-size: 1.5em;
  margin-top: 0;

}

.feature__body{
  padding: 0 3em 2em;
  font-weight: 700;
}

/* =============== SOCIAL PROOF =============== */
.section__testimonial{
  font-style: italic;
  font-weight: 500;
  color: #5e5e5e;
}

.testimonial{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.testimonial-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.testimonial-image-container{
  order: 1
}

.testimonial-image{
  width: 150px;
  border-radius: 50%;
}

.testimonial-quote{
  width: 85%;
}

.testimonial-info{
  order: 3;
  font-size: 0.8em;
  margin: 1.5em 0 0 3em;
}



footer{
  background-color: rgba(61, 61, 61, 1);
  color: rgba(255, 255, 255, 0.6);
  padding: 4em 0 3em;
  font-size: 0.8em;
  text-align: center;
}


/* =============== MEDIA QUERIES =============== */

@media (min-width: 280px){
  body{
    font-size: 1.1rem;
  }
    
}

@media (min-width: 350px){
  body{
    font-size: 1.6rem;
  }
    
}

@media (min-width: 600px){
  .testimonial-container{
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .testimonial-image-container{
    order: 1;
    width: 25%;
    display: flex;
    justify-content: flex-end;
  }
  
  .testimonial-quote{
    order: 2;
    width: 62%;
    max-width: 550px;
  }
  
  .testimonial-info{
    order: 3;
    font-size: 0.8em;
  }
  
}
